import { World, Vec2, Box } from "planck";
import { bottomMargin, sideMargin } from "../../constants";

export default class Map {
  world: World;
  width: number;
  height: number;

  constructor(world: World, width: number, height: number) {
    this.world = world;
    this.width = width - 2 * sideMargin; // 양 옆 마진을 고려하여 너비 조정
    this.height = height;

    // Create boundaries
    this.createBoundary(sideMargin, -height - bottomMargin, this.width, 1); // Top
    this.createBoundary(sideMargin, -bottomMargin, this.width, 1); // Bottom
    this.createBoundary(sideMargin, -height - bottomMargin, 1, this.height); // Left
    this.createBoundary(
      width - sideMargin,
      -height - bottomMargin,
      1,
      this.height
    ); // Right
  }

  private createBoundary(x: number, y: number, width: number, height: number) {
    const body = this.world.createBody({
      type: "static",
      position: Vec2(x + width / 2, y + height / 2), // 중심점을 기준으로 위치 설정
    });
    body.createFixture(Box(width / 2, height / 2), {
      density: 0.0,
      restitution: 1.0,
      friction: 0,
    });
  }
}
