import { Body, World, Box, Vec2 } from "planck";
import url from "../../../assets/bumper_rectangle_hor.png";

export default class BumperRect {
  // [x: string]: any;
  body: Body;
  image: HTMLImageElement;
  width: number;
  height: number;

  constructor(
    world: World,
    x: number,
    y: number,
    width: number,
    height: number
  ) {
    this.body = world.createBody({
      type: "static",
      position: Vec2(x, y),
    });
    this.width = width;
    this.height = height;
    this.body.createFixture(Box(width, height), {
      density: 1.0,
      friction: 0.3,
      restitution: 0.8,
    });
    // this.body.setAngle(Math.PI / 2);
    this.image = new Image();
    this.image.src = url;
    this.body.setUserData(this);
  }
}
