import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { Clouds } from "../../components/Clouds";
import "./style.css";
import { PlayerSelectTable } from "../../components/PlayerSelectTable";
import { Quit } from "../../components/Quit";
import { LaunchButton } from "../../components/LaunchButton";
import arrow_1 from "/static/img/arrow-1.svg";
import { bottomMargin, buttonColors, canvasWidth } from "../../constants";

// Player class which has ballspeed and color
class Player {
  ballSpeed: number;
  color: string;
  constructor(ballSpeed: number, color: string) {
    this.ballSpeed = ballSpeed;
    this.color = color;
  }
}

export const PlayerSelect = (): JSX.Element => {
  const [isHidden, setIsHidden] = useState(false);
  const [isHidden2, setIsHidden2] = useState(true);
  const [isHidden3, setIsHidden3] = useState(false);
  const [speed, setSpeed] = useState(0);
  const [length, setLength] = useState(50);
  const [width, setWidth] = useState(5);
  // ballSpeeds state 를 number 형태의 array 로 선언
  const [ballSpeeds, setBallSpeeds] = useState([] as number[]);
  const [players, setPlayers] = useState([] as Player[]);

  const MAX_PLAYERS = 8;
  const handleSpeedChange = (spd: number) => {
    if (spd >= 1) {
      setIsHidden(true);
      setSpeed(spd);
    }
  };

  const handleBallSpeedCompleted = (ballCount: number) => {
    if (speed >= 5) {
      setBallSpeeds((prevBallSpeeds) => {
        const newBallSpeeds = [...prevBallSpeeds, speed];
        // get window width

        let center = canvasWidth / 2;
        let px = center;
        let py = bottomMargin;
        // set player position
        if (ballCount < players.length / 2) {
          px -= 50 * (players.length / 2 - ballCount);
        } else {
          px += 50 * (players.length / 2 - ballCount);
        }
        let np = new Player(speed, buttonColors[players.length]);
        if (players.length < MAX_PLAYERS) {
          setPlayers((prevPlayers) => {
            return [...prevPlayers, np];
          });
          if (players.length == MAX_PLAYERS) {
            setIsHidden3(true);
          }
          return newBallSpeeds;
        } else {
          setIsHidden3(true);
          return prevBallSpeeds;
        }
      });
      console.log("ballCount", ballCount, "ballSpeeds", ballSpeeds);
      setIsHidden(false);
      setIsHidden2(false);
    }
    setSpeed(0);
  };

  useEffect(() => {
    setLength(50 + speed * 25);
    setWidth(5 + speed * 2.5);
  }, [speed]);

  useEffect(() => {
    if (players.length == MAX_PLAYERS) {
      setIsHidden3(true);
    }
  }, [players]);
  // const length = 50 + speed*0.5;
  // const width = 5 + speed * 0.05;

  return (
    <div className="playerselect" onContextMenu={(e) => e.preventDefault()}>
      <div className="top-bar">
        <div className="text-wrapper-2">gci.js</div>
        <div className="text-wrapper-3">2023</div>
      </div>
      <Quit />
      <div className="overlap-2">
        <Clouds
          className="clouds-instance"
          cloudClassName="design-component-instance-node"
          cloudClassNameOverride="clouds-3"
          overlapClassName="clouds-2"
          property1="default"
        />
        <PlayerSelectTable players={players} />

        <div className="go-to-screen2"></div>
        {/* <div id="convertableArrow2" style={arrowStyle} /> */}

        {/* <svg width={length} height={length} viewBox={`0 0 ${length} ${length}`}>
              <line x1="0" y1={length / 2} x2={length - width} y2={length / 2} stroke="red" strokeWidth={width} />
              <polygon points={`${length - width},${length / 2} ${length},${length / 2 - width} ${length},${length / 2 + width}`} fill="red" />
            </svg> */}

        <img
          className="player-arrow"
          alt="Arrow"
          src={arrow_1}
          style={{
            width: width,
            position: "absolute",
            bottom: "25vh",
            left: "50%", // 화면 가로 중앙에 위치
            transform: `translateX(-${width / 2}px)`, // 중앙 정렬을 위한 가로 이동
          }}
        />
        <div className="frame">
          <div
            className="main-title"
            style={{ display: isHidden ? "none" : "block" }}
          >
            <div className="overlap-group-5">
              <div className="flexcontainer-i">
                <div className="span-wrapper">
                  <span className="span">
                    버튼을 눌러 <br />
                  </span>
                </div>
                <div className="span-wrapper">
                  <span className="span">병뚜껑을</span>
                </div>
                <div className="span-wrapper">
                  <span className="span">발사하세요</span>
                </div>
                <div
                  className="text-i"
                  style={{ display: isHidden2 ? "none" : "block" }}
                >
                  <br />
                  <span className="text-wrapper">
                    <Link
                      to={"/playGame"}
                      state={{ ballSpeeds: ballSpeeds, players: players }}
                    >
                      준비완료
                    </Link>
                  </span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div
        className="launch-button-pos"
        style={{ display: isHidden3 ? "none" : "flex" }}
      >
        <LaunchButton
          id="11"
          onSpeedChange={handleSpeedChange}
          onBallSpeedCompleted={handleBallSpeedCompleted}
          buttonColor={buttonColors[players.length]}
        />
      </div>
    </div>
  );
};
