import PropTypes from "prop-types";
import React from "react";
import { Link } from "react-router-dom";
import "./style.css";
import bgm from "../../../pubilc/audio/main-bgm.mp3";

interface Props {
  property1: "variant-2" | "default";
  className: any;
}


const handlePlayAudio = () => {
    const audio = new Audio(bgm);
    audio.loop = true;
    audio.volume = 0.5;
    audio.play();
};

export const Start = ({ property1, className }: Props): JSX.Element => {
  return (
    <div className={`start ${className}`} onClick={handlePlayAudio}>
      <Link to="/playerSelect">
        <div className={`overlap-group-3 property-1-0-${property1}`}>
          <div className="start-button-shadow">Start Game</div>
          <div className="start-button">Start Game</div>
        </div>
      </Link>
    </div>
  );
};

Start.propTypes = {
  property1: PropTypes.oneOf(["variant-2", "default"]),
};
