import React, { useState, useRef } from "react";
import "./style.css";
interface ChildComponentProps {
  id: string;
  onSpeedChange: (spd: number) => void;
  onBallSpeedCompleted: (ballCount: number) => void;
  buttonColor: string;
}
export const LaunchButton: React.FC<ChildComponentProps> = ({ id, onSpeedChange, onBallSpeedCompleted, buttonColor }) => {
  const [speed, setSpeed] = useState(0);
  const [currentShapeIndex, setCurrentShapeIndex] = useState(0);
  const radius = 42.5;
  const maxSpeed = radius + 8; // 외곽선이 완전히 채워지는 속도
  const lineLength = radius - 5;
  const cx = 50;
  const cy = 50;

  // export const LaunchButton = (props: { id: string }): JSX.Element => {
  // const { id } = props;

  // const { startIncreasingSpeed, stopIncreasingSpeed, getSpeed } = useSpeedControl();

  // const [speed, setSpeed] = useState(0);
  const intervalId = useRef<number | undefined>();

  const startIncreasingSpeed = () => {
    console.log("startIncreasingSpeed is called");
    intervalId.current = window.setInterval(() => {
      setSpeed((prevSpeed) => {
        if (prevSpeed < maxSpeed) {
          const newSpeed = prevSpeed + 1;
          onSpeedChange(newSpeed);
          console.log("Interval is running!", newSpeed);
          return newSpeed;
        } else {
          return prevSpeed;
        }
      });
    }, radius + 5); // 500ms로 변경
    // setSpeed((prevSpeed) => { prevSpeed++; return prevSpeed; });
  };
  const stopIncreasingSpeed = () => {
    window.clearInterval(intervalId.current);
    setCurrentShapeIndex((prevIndex) => {
      const newIndex = prevIndex + 1;
      setSpeed(0);
      onBallSpeedCompleted(newIndex);
      return newIndex;
    });
    // intervalId = undefined;
  };

  const getSpeed = () => speed;

  const strokeLength = 308; // 외곽선의 길이 (원의 둘레)

  return (
    <div
      id={id}
      className="launch-button"
      onMouseDown={() => {
        console.log("Button is pressed");
        startIncreasingSpeed();
      }}
      onMouseUp={stopIncreasingSpeed}
      onMouseLeave={stopIncreasingSpeed}
      onTouchStart={startIncreasingSpeed}
      onTouchEnd={stopIncreasingSpeed}
      onTouchCancel={stopIncreasingSpeed}
      onContextMenu={(e) => e.preventDefault()}
    >
      <svg id="speedButtonSVG" viewBox="0 0 100 100">
        <circle id="speedButton" cx={cx} cy={cy} r={radius} fill={buttonColor} />
        {/* 가로선 그리기 */}
      <line x1={cx - lineLength / 2} y1={cy} x2={cx + lineLength / 2} y2={cy} stroke="white" strokeWidth="3" />
      {/* 세로선 그리기 */}
      <line x1={cx} y1={cy - lineLength / 2} x2={cx} y2={cy + lineLength / 2} stroke="white" strokeWidth="3" />
    
        {/* <circle cx={cx} cy={cy} r={radius + 5} fill="none" stroke="grey" strokeWidth="2">
          <animate
            id="twinkle"
            attributeName="stroke-opacity"
            values="0;1;0"
            dur="1s"
            repeatCount="indefinite"
            begin="indefinite"
          />
        </circle> */}
        <circle
          id="outline"
          cx={cx}
          cy={cy}
          r={radius + 5}
          fill="none"
          stroke="white"
          strokeWidth="2"
          strokeDasharray={strokeLength}
          strokeDashoffset={
            strokeLength - (strokeLength * getSpeed()) / maxSpeed
          } // 25는 최대 속도
        />
      </svg>
    </div>
  );
};
