import { Ball } from "./elements/ball";
import Booster from "./elements/booster";
import Bumper from "./elements/bumper";
import BGurl from "../../assets/PlayBG.png";
import {
  canvasWidth,
  canvasHeight,
  bottomMargin,
  sideMargin,
  initialZoom,
  startLineMargin,
} from "../constants";
import Map from "./elements/map";
import { Camera } from "./elements/camera";
import BumperRect from "./elements/bumperRect";

export default class AlkagiRenderer {
  private canvas!: HTMLCanvasElement;
  private context!: CanvasRenderingContext2D;
  private bgImage: HTMLImageElement;
  boosters: Booster[] = [];
  bumpers: Bumper[] = [];
  bumperRects: BumperRect[] = [];
  balls: Ball[] = [];

  constructor(canvas: HTMLCanvasElement) {
    this.bgImage = new Image();
    this.bgImage.src = BGurl;
    this.bgImage.onload = () => {
      console.log("Background image loaded successfully.");
    };

    this.canvas = canvas;
    this.context = this.canvas.getContext("2d")! as CanvasRenderingContext2D;
    this.init();
  }

  addBooster(booster: Booster) {
    this.boosters.push(booster);
  }

  addBumper(bumper: Bumper) {
    this.bumpers.push(bumper);
  }

  addBumperRect(bumperRect: BumperRect) {
    this.bumperRects.push(bumperRect);
  }

  addBall(ball: Ball) {
    this.balls.push(ball);
  }

  init() {
    this.canvas.width = canvasWidth;
    this.canvas.height = canvasHeight;
    this.context.translate(0, canvasHeight);
  }

  render(map: Map, camera: Camera) {
    this.context.fillStyle = "blue";
    this.context.save();
    this.context.scale(initialZoom, initialZoom);

    this.context.clearRect(0, -5000, this.canvas.width, 5000);

    camera.renderScene(this.context, () => {
      this.renderBG();
      this.renderMapWall(map);
      this.renderBoosters();
      this.renderBumpers();
      this.renderBalls();
      this.renderBumperRects();
    });
    this.context.restore();
  }
  private renderBG() {
    const newWidth = canvasWidth - 2 * sideMargin;
    const newHeight = newWidth * 10;

    const imgX = sideMargin;
    const imgY = -bottomMargin - newHeight; // 이미지의 하단이 bottomMargin 위치에 오도록

    this.context.drawImage(this.bgImage, imgX, imgY, newWidth, newHeight);
  }
  private renderBumpers() {
    for (const bumper of this.bumpers) {
      const position = bumper.body.getPosition();
      const radius = bumper.radius; // Assuming you have a radius property in Bumper
      const image = bumper.image;

      // 이미지를 그릴 때 radius만큼 위치를 조정
      const x = position.x - radius;
      const y = position.y - radius;

      this.context.fillStyle = "#000000"; // 배경색 설정. 필요에 따라 색상을 변경하세요.
      this.context.beginPath();
      this.context.arc(position.x, position.y, radius, 0, Math.PI * 2);
      this.context.fill();

      // 이미지 그리기
      this.context.drawImage(image, x, y, radius * 2, radius * 2);
    }
  }
  private renderBumperRects() {
    for (const bumperRect of this.bumperRects) {
      const position = bumperRect.body.getPosition();
      const width = bumperRect.width; // Assuming you have a radius property in Bumper
      const height = bumperRect.height;
      const image = bumperRect.image;

      // 이미지를 그릴 때 radius만큼 위치를 조정
      const x = position.x - width / 2;
      const y = position.y;

      // 이미지 그리기
      this.context.drawImage(image, x, y, width, height); // 이미지 그리기
    }
  }
  private renderBoosters() {
    for (const booster of this.boosters) {
      const position = booster.body.getPosition();
      const width = booster.width;
      const height = booster.height;
      const image = booster.image;

      const x = position.x - width / 2;
      const y = position.y - height / 2;

      // this.context.fillStyle = "#000000"; // 배경색 설정. 필요에 따라 색상을 변경하세요.
      // this.context.fillRect(x, y, width, height); // 부스터의 배경을 그림

      this.context.drawImage(image, x, y, width, height); // 이미지 그리기
    }
  }
  private renderBalls() {
    for (const ball of this.balls) {
      ball.draw(this.context);
    }
  }
  private renderMapWall(map: Map) {
    const topY = -map.height - bottomMargin; // Map의 높이에서 bottomMargin을 뺀 값
    const bottomY = -bottomMargin; // bottomMargin만큼 위로 올린 값

    this.context.strokeStyle = "#ffffff"; // 검은색으로 테두리를 그립니다.
    this.context.lineWidth = 2; // 테두리 두께를 2로 설정합니다.

    // 상단 테두리 그리기
    this.context.beginPath();
    this.context.moveTo(sideMargin, topY);
    this.context.lineTo(sideMargin + map.width, topY);
    this.context.stroke();

    // 하단 테두리 그리기
    this.context.beginPath();
    this.context.moveTo(sideMargin, bottomY);
    this.context.lineTo(sideMargin + map.width, bottomY);
    this.context.stroke();

    // 좌측 테두리 그리기
    this.context.beginPath();
    this.context.moveTo(sideMargin, topY);
    this.context.lineTo(sideMargin, bottomY);
    this.context.stroke();

    // 우측 테두리 그리기
    this.context.beginPath();
    this.context.moveTo(sideMargin + map.width, topY);
    this.context.lineTo(sideMargin + map.width, bottomY);
    this.context.stroke();

    // 춟발선 그리기
    this.context.beginPath();
    this.context.moveTo(sideMargin, -bottomMargin - startLineMargin);
    this.context.lineTo(
      sideMargin + map.width,
      -bottomMargin - startLineMargin
    );
    this.context.stroke();
  }
}
