import { Body, World, Vec2, Box } from "planck";
import url from "../../../assets/Booster_Plus.png";

export default class Booster {
  body: Body;
  boostVector: Vec2;
  image: HTMLImageElement;
  width: number;
  height: number;

  constructor(
    world: World,
    x: number,
    y: number,
    width: number,
    height: number,
    boostVector: Vec2
  ) {
    this.body = world.createBody({
      type: "static",
      position: Vec2(x, y),
    });
    this.width = width;
    this.height = height;

    const fixture = this.body.createFixture(Box(width, height), {
      isSensor: true, // 실제 물리적인 충돌은 일으키지 않는 센서 설정
    });

    this.boostVector = boostVector;
    this.image = new Image();
    this.image.src = url;
    this.body.setUserData(this);
  }
}
