import React, { useEffect, useRef, useState } from "react";
import { useLocation } from "react-router-dom";
import Alkagi, { BallRank } from "../../canvas/alkagi";
import { Link } from "react-router-dom";
import "./style.css";
import { buttonColors } from "../../constants";

export const PlayGame = (): JSX.Element => {
  const canvasRef = useRef<HTMLCanvasElement | null>(null);
  const location = useLocation();
  const receivedState = location.state;
  const rank = ["1st", "2nd", "3rd", "4th", "5th", "6th", "7th", "8th"];
  const [alkagiGame, setAlkagiGame] = useState<Alkagi | null>(null);
  const [ballRanks, setBallRanks] = useState<BallRank[]>([]);
  const [fontSize, setFontSize] = useState<string>("32px");
  const [countdown, setCountdown] = useState<number | string | null>(
    "준비하시고..."
  );
  const [timeLeft, setTimeLeft] = useState<number>(20);
  const [rankingName, setRankingName] = useState("ranking");
  const [rankName, setRankName] = useState("rank");
  const [replayVisible, setReplayVisible] = useState(false);
  const [timeLeftVisible, setTimeLeftVisible] = useState(true);

  useEffect(() => {
    // 카운트다운 로직
    const countdownInterval = setInterval(() => {
      setCountdown((prev) => {
        if (prev === "준비하시고...") {
          setFontSize("130px");
          return 3;
        }
        if (prev === 3) return 2;
        if (prev === 2) return 1;
        if (prev === 1) return "Go!";
        if (prev === "Go!") {
          setCountdown(""); // "Go!"가 표시된 후 일정 시간이 지나면 카운트다운을 화면에서 제거
          clearInterval(countdownInterval);
          return prev;
        }
        return prev;
      });
    }, 1000); // 1초 간격으로 카운트다운

    return () => clearInterval(countdownInterval); // 컴포넌트가 언마운트될 때 인터벌을 종료
  }, []);

  useEffect(() => {
    if (canvasRef.current) {
      const alkagiGame = new Alkagi(
        canvasRef.current,
        receivedState["ballSpeeds"],
        receivedState["players"],
        setBallRanks
      );
      setAlkagiGame(alkagiGame);
    }
  }, []);

  useEffect(() => {
    if (countdown === "" && alkagiGame) {
      alkagiGame.update();
    }
    const timer = setInterval(() => {
      setTimeLeft((prevTime) => {
        if (prevTime <= 1) {
          clearInterval(timer);
          // alkagiGame!.stopUpdate(); // alkagiGame에 stopUpdate 메서드가 있다고 가정
          setRankName("big-rank");
          setRankingName("big-ranking");
          setReplayVisible(true);
          setTimeLeftVisible(false);
          return 0;
        }
        return prevTime - 1;
      });
    }, 1000);
    return () => clearInterval(timer);
  }, [countdown, alkagiGame]);

  return (
    <div className="game-container">
      <canvas ref={canvasRef}></canvas>
      <div className="gradient" />
      <div className="top-bar">
        <div className="left-corner-title">gci.js</div>
        <div className="right-corner-title">2023</div>
      </div>

      <div className={rankingName}>
        {rankName == "rank" &&
          ballRanks.map((ballRank, index) => (
            <div className={rankName} key={index}>
              {rank[index]}: P{ballRank.index + 1}
            </div>
          ))}
        {rankName == "big-rank" &&
          ballRanks.map((ballRank, index) => (
            <div className="player-rank">
              <div className={rankName} key={index}>
                {rank[index]}:
              </div>
              <div
                className="player-icon"
                style={{ backgroundColor: buttonColors[index] }}
              >
                <div className="player-number">P{index + 1}</div>
              </div>
            </div>
          ))}
      </div>
      {timeLeftVisible && (
        <div className="time-left">Time Left: {timeLeft}s</div>
      )}
      {countdown && (
        <div className="countdown" style={{ fontSize: fontSize }}>
          {countdown}
        </div>
      )}
      {replayVisible && (
        <Link to="/playerSelect">
          <div className="replay-button">
            <div className="replay">Replay</div>
            <div className="replay">다시하기</div>
          </div>
        </Link>
      )}
    </div>
  );
};
