import { Body, World, Circle, Vec2 } from "planck";
import url from "../../../assets/Bumper_Circle_2.png";

export default class Bumper {
  // [x: string]: any;
  body: Body;
  image: HTMLImageElement;
  radius: number;

  constructor(world: World, x: number, y: number, radius: number) {
    this.body = world.createBody({
      type: "static",
      position: Vec2(x, y),
    });
    this.radius = radius;
    this.body.createFixture(Circle(radius), {
      density: 1.0,
      friction: 0.3,
      restitution: 1.0,
    });
    this.image = new Image();
    this.image.src = url;
    this.body.setUserData(this);
  }
}
