import { World, Vec2 } from "planck";
import Bumper from "./elements/bumper";
import Booster from "./elements/booster";
import BumperRect from "./elements/bumperRect";
import {
  bottomMargin,
  canvasHeight,
  canvasWidth,
  mapHeight,
  sideMargin,
} from "../constants";

function getRandom(min: number, max: number): number {
  return Math.random() * (max - min) + min;
}
function isValidPosition(
  newPos: Vec2,
  existingObjects: (Bumper | Booster)[],
  minDistance: number
): boolean {
  for (const obj of existingObjects) {
    if (Vec2.distance(newPos, obj.body.getPosition()) < minDistance) {
      return false;
    }
  }
  return true;
}
export function createObjects(
  world: World,
  numBumpers: number,
  numBoosters: number
) {
  const existingObjects: (Bumper | Booster)[] = [];
  const minDistance = 50; // 충돌을 피하기 위한 최소 거리. 이 값은 조절할 수 있습니다.

  var bumpers: Bumper[] = [];
  for (let i = 0; i < numBumpers; i++) {
    let attempt = 0;
    let placed = false;

    while (attempt < 10 && !placed) {
      const x = getRandom(sideMargin + 15, canvasWidth - sideMargin - 15);
      const y = getRandom(-mapHeight + bottomMargin, -bottomMargin - 300);
      if (isValidPosition(Vec2(x, y), existingObjects, minDistance)) {
        const radius = getRandom(20, 30);
        const bumper = new Bumper(world, x, y, radius);
        bumpers.push(bumper);
        existingObjects.push(bumper);
        placed = true;
      }
      attempt++;
    }
  }

  var bumperRects: BumperRect[] = [];
  const x = (canvasWidth * 1) / 2;
  const y = -mapHeight - bottomMargin;
  const width = canvasWidth * 1 - sideMargin;
  const height = 80;
  var bumperRect = new BumperRect(world, x, y, width, height);
  bumperRects.push(bumperRect);
  // bumperRects.push(new BumperRect(world, x, y, width, height));
  // bumperRects.push(new BumperRect(world, x, -500, width, height));

  var boosters: Booster[] = [];
  for (let i = 0; i < numBoosters; i++) {
    let attempt = 0;
    let placed = false;

    while (attempt < 10 && !placed) {
      const x = getRandom(sideMargin + 10, canvasWidth - sideMargin - 10);
      const y = getRandom(-mapHeight + bottomMargin, -bottomMargin);
      if (isValidPosition(Vec2(x, y), existingObjects, minDistance)) {
        const boostDirection = new Vec2(getRandom(-1, 1), getRandom(-1, 1));
        const booster = new Booster(world, x, y, 20, 20, boostDirection);
        boosters.push(booster);
        existingObjects.push(booster);
        placed = true;
      }
      attempt++;
    }
  }

  return { bumpers, boosters, bumperRects };
}
